.title--single {
  margin-top: -9rem;
  max-width: 600px;
  font-size: 8rem;
  text-align: center;
  word-wrap: normal; }
  @media all and (max-width: 600px) {
    .title--single {
      font-size: 6rem; } }
  @media all and (max-width: 500px) {
    .title--single {
      margin: 20px;
      margin-top: -9rem;
      font-size: 4rem; } }

input, textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #3d3d3f; }
  input.input--error, textarea.input--error {
    border-color: red; }

.hidden {
  display: none; }

label {
  padding: 10px 0;
  display: block;
  font-family: 'Montserrat', sans-serif; }

.form {
