  .form h2 {
    margin: 0;
    font-size: 2rem;
    padding-bottom: 2rem;
    margin-bottom: 2rem;
    border-bottom: 1px solid #3d3d3f; }

.search {
  position: relative;
  width: 100%;
  display: flex; }
  .search__results {
    background: white;
    position: absolute;
    width: 100%;
    top: 100%;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    z-index: 5;
    left: 0; }
  .search__input {
    font-size: 18px;
    outline: 0;
    border: 0;
    background: none;
    border-bottom: 1px solid #3d3d3f; }
  .search__result {
    padding: 10px;
    display: block;
    border-bottom: 1px solid #ececec; }
    .search__result--active {
      background: #f1f1f1; }

@keyframes grow {
  0% {
    width: 0px; }
  100% {
    width: 50px; } }

@keyframes SlideIn {
  0% {
    transform: translateX(-10%); }
  100% {
    transform: translateX(0%); } }

.flash {
  margin-bottom: 2rem;
  padding: 1.5rem;
  margin-left: 50px;
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: center;
  animation: SlideIn 0.3s; }
  .flash + .flash {
    animation-delay: 0.55s; }
    .flash + .flash + .flash {
      animation-delay: 0.6s; }
  .flash p:before {
    content: '';
    display: block;
    background: #3d3d3f;
    position: relative;
    width: 0px;
    height: 1px;
    top: 50%;
    transform: translateY(-50%);
    left: -60px;
    z-index: -1;
    transform-origin: 0%;
    animation: grow 0.3s forwards;
    animation-delay: 0.3s; }
  .flash--success p:before {
    background: #7dce94; }
  .flash--error p:before {
    background: red; }
  .flash--info p:before {
    background: #FFC40E; }
  .flash p {
    margin: 0; }
  .flash__remove {
    background: none;
    border: 0; }
    .flash__remove:hover {
      color: #FFC40E; }

.nav {
  display: flex;
  list-style: none;
  margin: 10px;
  padding: 0;
  flex-direction: column; }
  .nav__section {
    display: none;
    height: auto; }
    .nav__section--search {
      flex: 1 1 auto;
      order: 2;
      margin-top: 10px;
      background-color: white; }
    .nav__section--pages .nav__item, .nav__section--user .nav__item {
      padding-top: 20px; }
    .nav__section--logo {
      display: block; }
  .nav .is-expanded {
    display: block; }
  .nav__item {
    display: flex;
    justify-content: space-around; }
    .nav__item span {
      display: inline-block;
      position: relative; }
    .nav__item span:before {
      content: '';
      position: absolute;
      width: 100%;
      height: 0px;
      border-bottom: 1px solid black;
      bottom: -2px;
      transform: scaleX(0);
      transition: all 0.2s ease-in; }
    .nav__item span:hover:before {
      transform: scaleX(1); }
  .nav__link {
    color: #3d3d3f;
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    padding: 0 2rem 0 2rem;
    display: flex;
    flex-direction: column;
    white-space: nowrap;
    align-items: center;
    justify-content: center;
    transition: transform 0.2s; }
    .nav__link--logo {
      width: 250px; }
  .nav .burger--container {
    position: absolute;
    left: 10px;
    top: 10px;
    width: 4rem;
    height: 4rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #3d3d3f;
    padding: 1rem;
    cursor: pointer;
    transition: padding 100ms ease; }
    .nav .burger--container.is-expanded {
      padding: calc(2rem - 2px) 1rem; }
  .nav .burger--line {
