    flex-direction: row;
    flex-wrap: wrap; }
    .nav__section {
      display: flex; }
      .nav__section--search {
        margin-top: 10px;
        border-top: 1px solid rgba(255, 255, 255, 0.1);
        order: 3;
        width: 100%; }
      .nav__section--pages .nav__item, .nav__section--user .nav__item {
