.single {
    &__hero {
        height: 400px;
        position: relative;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
        clip-path: polygon(0 0, 100% 0, 100% calc(100% - 2vw), 0% 100%);
        @media all and (max-width: 600px) {
            height: 300px;
        }
    }
    &__image {
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        object-fit: cover;
        position: absolute;
    }
    &__details {
        background: white;
        // clip-path: polygon(0px 2vw, 100% 0, 100% calc(100% - 2vw), 0% 100%);
        position: relative;
        padding: 3rem;
        margin-top: -10rem;
        box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.15);
    }
    &__map {
        width: calc(100% + 6rem);
        max-width: none;
        margin-top: -3rem;
        margin-left: -3rem;
        @media all and (max-width: 1100px) {
            width: calc(100% + 6rem);
        }
    }
    &__location {
        margin: 0;
        margin-top: -3rem;
        margin-right: -5rem;
        background: $black;
        color: white;
        display: inline-block;
        float: right;
        position: relative;
        padding: 1rem;
        @media all and (max-width: 1100px) {
            margin-right: 0;
        }
    }
}


