.tags {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    margin-bottom: 2rem;
    margin-top: 1rem;
    flex-wrap: wrap;
}

.tag {
    display: block;
    margin-right: 1rem;
    &__choice {
        display: flex;
        justify-content: center;
        align-items: center;
        input {
            cursor: pointer;
            width: auto;
            right: -1.5rem;
            margin-right: -1rem;
            position: relative;
            z-index: 2;
            &+label {
                background: $lightBrown;
                padding: 1rem 1rem 1rem 4rem;
            }
            &:checked+label {
                background: $green;
            }
        }
    }
    &__link {
        background: $lightBrown;
        color: $black;
        display: flex;
        padding-left: 10px;
        clip-path: polygon(10px 0%, 100% 1%, 100% 100%, 10px 100%, 0% 50%);
        &--active {
            background: $green;
            .tag__text {
                color: $black;
            }
        }
    }
    &__text {
        padding: 1rem 1rem;
        display: block;
    }
    &__count {
        padding: 1rem 1rem;
        background: white;
        border-left: 3px solid rgba(0, 0, 0, 0.1);
        margin-right: -1px;
        /* ugh */
        background-clip: padding-box;
    }
}


