@import url('https://fonts.googleapis.com/css?family=Crimson+Text|Montserrat:400,700|Open+Sans');
html {
    font-size: 10px;
}

body {
    font-size: 1.5rem;
    color: $black;
}

h1,
h2,
h3,
h4,
h5,
h6,
{
    font-size: 5rem;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    color: $grey;
    @media all and (max-width: 500px) {
        font-size: 4rem;
    }
}

a {
    text-decoration: none;
    color: $black;
}

p a {
    border-bottom: 2px solid $yellow;
}

p {
    line-height: 2;
    font-size: 1.6rem;
}

.title {
    word-wrap: break-word;
    position: relative;
    z-index: 2;
    margin: 0;
    font-size: 40px;
    line-height: 1.1;
    transform: skew(0, -3deg);
    border: 6px solid $black;
    background-color: $lightBrown;
    display: inline-block;
    &--long {
        font-size: 30px;
    }
    &:before {
        content: '';
        width: 50px;
        left: 0;
        top: 0;
        height: 100%;
        // top:10%;
        display: block;
        position: absolute;
        z-index: -1;
        transform: skew(-5deg)
    }
    line-height:1.1;
    // inline link inside
    a {
        // $yellowa: rgba($yellow,0.8);
    }
}

.title--single {
    margin-top: -9rem;
    max-width: 600px;
    font-size: 8rem;
    text-align: center;
    word-wrap: normal;
    @media all and (max-width: 600px) {
        font-size: 6rem;
    }
    @media all and (max-width: 500px) {
        margin: 20px;
        margin-top: -9rem;
        font-size: 4rem;
    }
}


